<template>
  <v-app dark>
    <v-app-bar app color="rgb(255,153,0)" dark>
      <div class="d-flex align-center">
        <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
        <!--                <v-img-->
        <!--                  alt="Vuetify Logo"-->
        <!--                  class="shrink mr-2"-->
        <!--                  contain-->
        <!--                  src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"-->
        <!--                  transition="scale-transition"-->
        <!--                  width="40"-->
        <!--                />-->
      </div>
      <v-spacer></v-spacer>
      <v-btn rounded color="warning" @click="back">返回</v-btn>
      <!--      <v-spacer></v-spacer>-->
      <!--      <v-btn-->
      <!--        href="https://github.com/vuetifyjs/vuetify/releases/latest"-->
      <!--        target="_blank"-->
      <!--        text-->
      <!--      >-->
      <!--        <span class="mr-2">Latest Release</span>-->
      <!--        <v-icon>mdi-open-in-new</v-icon>-->
      <!--      </v-btn>-->
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app temporary>
      <v-list nav dense>
        <v-list-item-group v-model="group" active-class="text--accent-4">
          <v-list-item v-for="(item, i) in items" :key="i" @click="goRouter(item.link)">
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-main>
      <v-banner dark v-if="showAd" style="margin-bottom: .1rem">
        <span>这里是公告。</span>
        <template v-slot:actions>
          <v-btn text @click="showAd=false">我知道了</v-btn>
        </template>
      </v-banner>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>

import {mapMutations} from "vuex";

export default {
  name: "App",
  data: () => ({
    drawer: false,
    group: 0,
    items: [],
    showAd: true
  }),
  watch: {
    group(newVal) {
      this.drawer = false;
    },
    $route: {
      handler(newVal) {
        this.items.forEach((v, index) => {
          if (v.link === newVal.path) {
            this.group = index;
          }
        });
      }
    }
  },
  methods: {
    ...mapMutations(['setWindowWidth', 'setWindowHeight', 'setClientType']),
    goRouter(link) {
      this.$router.push(link);
    },
    back() {
      if (this.$route.path != '/' && this.$route.path != '/Home') {
        this.$router.go(-1)
      } else {
        this.group = 0;
      }
    }
  },
  created() {
    const routeList = this.$router.getRoutes();
    let routers = [];
    routeList.forEach(v => {
      if (!v.meta.hidden) routers.push({title: v.meta.title, link: v.path});
    });
    this.items = routers;
  },
  mounted() {
    this.setClientType();
    window.onresize = () => {
      return (() => {
        this.setClientType();
        this.setWindowWidth();
        this.setWindowHeight();
      })();
    };
  }
};
</script>
