import Vue from 'vue'
import Vuex from 'vuex'
import UA from "ua-device";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    windowWidth: document.documentElement.clientWidth,
    windowHeight: document.documentElement.clientHeight,
    clientType: 0, //0 mobile 1 desktop
  },
  mutations: {
    setWindowWidth(state) {
      state.windowWidth = document.documentElement.clientWidth;
    },
    setWindowHeight(state) {
      state.windowHeight = document.documentElement.clientHeight;
    },
    setClientType(state) {
      let output = new UA(navigator.userAgent);
      if (output.device.type == 'mobile') {
        state.clientType = 0;
      } else {
        state.clientType = 1;
      }
    }
  },
  actions: {},
  modules: {}
})
